import { NavigateFunction, NavigateOptions } from "react-router-dom"
import { Product } from "@rp/common/api-types"
import { ADD_CONTENT_ROUTE, CONTACT_SALES_ROUTE, UPGRADE_ROUTE } from "./routeConstants"

const getUpgradePlanUrl = (
  currentUser: "free" | "pro",
  product?: Product,
  isEnterprise?: boolean,
  subscriptionFrequency?: string,
) => {
  if (isEnterprise) {
    return CONTACT_SALES_ROUTE
  }

  return `${currentUser === "free" ? UPGRADE_ROUTE : ADD_CONTENT_ROUTE}${
    product
      ? `?product=${product}&frequency=${
          subscriptionFrequency ? subscriptionFrequency.toLocaleLowerCase() : "yearly"
        }`
      : ""
  }`
}

const redirectToUpdatePlan = (
  navigate: NavigateFunction,
  currentUser: "free" | "pro",
  product: Product,
  isEnterprise?: boolean,
  subscriptionFrequency?: string,
  options?: NavigateOptions,
) => {
  navigate(getUpgradePlanUrl(currentUser, product, isEnterprise, subscriptionFrequency), options)
}

export { redirectToUpdatePlan, getUpgradePlanUrl }
